<script>
import Vue from 'vue';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import queryString from "query-string";
import _ from 'lodash';
import 'balloon-css';
import DataHandler from './data_handler.js'
import Overview from "./components/Overview.vue";
import StepByStep from "./components/StepByStep.vue";
import Payment from "./components/Payment.vue";
import Question from './components/Question.vue';
import Creation_examples from "./components/Creation_examples"

document.title = "Creation task"

export default {
  name: 'app',
  components: {
    'Overview': Overview,
    'Creation_examples': Creation_examples,
    'Question': Question,
    'StepByStep': StepByStep,
    'Payment': Payment,
  },

  data: function(){
    return {
        active_tab: "overview",

        creation_seeds: [],
        results: "",
        num_generated: 0,
        pass_num: 0,

        mturk_params: {
          assignmentId: "",
          hitId: "",
          turkSubmitTo: "",
          workerId: "",
          isPreview: true,
        },

        generated_answers: {    // JSON file   
          0: "",
          1: "",
          2: "",
          3: "",
          4: "",
          5: "",
          6: "",
          7: "",
          8: "",
          feedback: "",
        },

        show_submission_alert: false,

        number_of_questions: 10,
        number_of_additions: 5,
        stage: 0,

        bonus_per_question: 0.7,
        prev_pass: 0,
        prev_add: 0,
        prev_questions: 0,

        data_url: "https://creation-raw-data-storage.click/data",

        host_name: "",
        feedback: "",
        loading: false,
        submitting: false,
        submitted: false,

        addition: false,
        add_times: 0,
        max_add_times: 8,

        error_occurred: false,
    }
  },

  filters: {          // apply common text formatting. be usable in two places: mustache interpolations and v-bind.
    currency: function(value){
      return value.toFixed(2);      //   2 .
    }
  },

  computed: {
    show_tab_overview: function(){
      return this.active_tab === "overview"
    },
    show_tab_step_by_step: function(){
      return this.active_tab === "step-by-step"
    },
    show_tab_pay_plan: function(){
      return this.active_tab === "pay-plan"
    },
    show_tab_real_creation: function(){
      return this.active_tab === "real-creation"
    },

    total_payment: function(){              // qualification task total pay.
      return (this.prev_add + this.add_count) * this.bonus_per_question;
    },

    local_debug_mode: function(){       // flask backend database server ip?
      return this.host_name === "localhost"
      // return true
    },

    all_answered(){
      return _.every(this.creation_seeds, (sample) => {
        let item = Object.values(sample)[0]
        if (item.passed === true) {
          return item.pass_reason !== ''
        }
        else {
          return item.question !== '' && item.correct !== '' && item.incorrect !== '' && item.new_question !== ''
              && item.val1 !== false && item.val2 !== false && item.val3 !== false && item.val4 !== false
        }
      })
    },

    pass_count(){
      let count = 0;
      _.each(this.creation_seeds, (sample) => {
        let item = Object.values(sample)[0]
        if (item.passed === true && item.pass_reason !== '') {
          count += 1;
        }
      })
      return count
    },

    add_count(){
      let count = 0;
      _.each(this.creation_seeds, (sample) => {
        let item = Object.values(sample)[0]
        if (item.passed === false
            && item.question !== '' && item.correct !== '' && item.incorrect !== '' && item.new_question !== ''
            && item.val1 !== false && item.val2 !== false && item.val3 !== false && item.val4 !== false) {
          count += 1;
        }
      })
      return count
    },

    not_yet(){
      return this.prev_questions+this.number_of_questions - (this.prev_add + this.add_count) - (this.prev_pass + this.pass_count)
    },

    not_answered(){
      const verification = [];
      _.each(this.creation_seeds, (sample) => {
        let item = Object.values(sample)[0]
        if (item.passed === true) {
          verification.push(item.pass_reason !== '')
        }
        else {
          verification.push(item.question !== '' && item.correct !== '' && item.incorrect !== '' && item.new_question !== ''
              && item.val1 !== false && item.val2 !== false && item.val3 !== false && item.val4 !== false)
        }
      })

      let not_answered_question = []
      for(let i=0; i<Object(verification).length; i++ ){
        if (verification[i] === false){
          not_answered_question.push(i+1)
        }
      }
      return not_answered_question
    },

    no_more_add: function(){
      return this.max_add_times === this.add_times
    }
  },

   mounted: async function(){
      const parsed_query_string = queryString.parse(window.location.search);
      console.log("parsed_query_string", parsed_query_string);
      this.host_name = window.location.hostname
      if (this.local_debug_mode){
        console.log("This is the local debug mode.")
      }

      let assignmentId = parsed_query_string.assignmentId;
      if(assignmentId === null || assignmentId === undefined){
        assignmentId = "TestAssignmentId";
      }
      if (assignmentId === "ASSIGNMENT_ID_NOT_AVAILABLE"){
        console.log("Preview mode.");
        this.mturk_params.isPreview = true;
      }

      const hitId = parsed_query_string.hitId;
      const turkSubmitTo = parsed_query_string.turkSubmitTo;
      let workerId = parsed_query_string.workerId;
      if(workerId === null || workerId === undefined || workerId === ""){
        workerId = "TestUser";
      }

      this.mturk_params.turkSubmitTo = turkSubmitTo + "/mturk/externalSubmit";
      this.mturk_params.workerId = workerId;
      this.mturk_params.hitId = hitId;
      this.mturk_params.assignmentId = assignmentId;

      this.active_tab = 'tab' in parsed_query_string ? parsed_query_string.tab : "overview";
      if(this.active_tab!=="overview"
          &&this.active_tab!=="step-by-step"
          &&this.active_tab!=="pay-plan"
          &&this.active_tab!=="real-creation"){
        this.active_tab = "overview";
      }
      console.log("mturk_params: ", this.mturk_params)

      if("data_url" in parsed_query_string){
        this.data_url = parsed_query_string.data_url;
      }

      let preliminary_data = await DataHandler.fetchQuestions(this.number_of_questions, this.data_url)
      this.load_seeds_csvs(preliminary_data);   // initialize this.creation_seeds
  },

  methods: {
    load_seeds_csvs: function(pre_data){
      this.creation_seeds = [];
      var data = _.map(pre_data, (example) => {
        example.number_of_questions = this.number_of_questions
        example.incorrect = ''
        example.new_question = ''
        example.val1 = false
        example.val2 = false
        example.val3 = false
        example.val4 = false
        example.passed = false
        example.pass_reason = ''
        return example;
      });
      for(let i=0; i<data.length; i++ ){
        const temp = {};
        temp[i+1] = data[i]
        this.creation_seeds.push(temp)
      }
      console.log("creation_seeds:", this.creation_seeds)
      // console.log(this.creation_seeds[0])
    },

    load_additional: async function(){

      this.loading = true;

      //   
      this.results = this.shape_answer(this.creation_seeds);           // make shaped this.results from this.creation_seeds
      this.generated_answers[this.add_times] = JSON.stringify(this.results)
      let feedback_shaped = {};
      feedback_shaped[this.mturk_params.workerId] = this.feedback;
      if (!this.local_debug_mode){
        await DataHandler.writeToS3(this.results, feedback_shaped)
      }

      this.add_times += 1;

      //   
      this.prev_pass += this.pass_count
      this.prev_add += this.add_count
      this.prev_questions += this.number_of_questions

      this.number_of_questions = this.number_of_additions;
      let preliminary_data = await DataHandler.fetchQuestions(this.number_of_questions, this.data_url)
      this.load_seeds_csvs(preliminary_data);   // initialize this.creation_seeds

      this.stage = 0;
      this.loading = false;
      document.getElementById("workspace_title").scrollIntoView({ behavior: 'smooth'});

      this.addition = true
    },

    switch_tab: function(tab){
      this.active_tab = tab;
    },

    pre_question: function(){
      document.getElementById("workspace_title").scrollIntoView({ behavior: 'smooth'});
      if (this.stage === 0){
        this.stage = this.number_of_questions-1;
      }
      else{
        this.stage -= 1;
      }
    },

    next_question: function(){
      document.getElementById("workspace_title").scrollIntoView({ behavior: 'smooth'});
      if (this.stage === this.number_of_questions-1){
        this.stage = 0;
      }
      else{
        this.stage += 1;
      }
    },


    shape_answer: function(samples_array){
      var results = {};
      for (let i=0; i<samples_array.length; i++){
        var sample = samples_array[i]
        sample = sample[i+1]            // dictionary
        results[sample['question_id']] = {
          'workerId': this.mturk_params.workerId,
          'AssignmentId': this.mturk_params.assignmentId,
          'question': sample['question'],
          'correct': sample['correct'],
          'incorrect': sample['incorrect'],
          'new_question': sample['new_question'],
          'passed': sample['passed'],
          'pass_reason': sample['pass_reason'],
        }
      }
      return results
    },

    submitToUs: async function(){
      this.loading = true;
      this.submitting = true;

      this.results = this.shape_answer(this.creation_seeds);           // make shaped this.results from this.creation_seeds
      this.generated_answers[this.add_times] = JSON.stringify(this.results)
      this.generated_answers['feedback'] = JSON.stringify(this.feedback)

      let feedback_shaped = {};
      feedback_shaped[this.mturk_params.workerId] = this.feedback;

      console.log(this.mturk_params.assignmentId)
      console.log(this.total_payment)
      console.log(this.generated_answers)

      if (!this.local_debug_mode){
        await DataHandler.writeToS3(this.results, feedback_shaped)
      }
      
      this.loading = false;
      this.submitting = false;
      this.submitted = true;
    },

    submitToTurk: async function(){
      if(this.local_debug_mode){
        console.log("Local testing mode. Not submitting to mturk.");
      }
      else{
        Vue.nextTick(function () {
          document.getElementById('mturk-external-submit-form').submit();
        });
      }
    },
  },

}
</script>

